import Vue from 'vue'
import { mapGetters, mapActions } from 'vuex'
import Encoder from 'code-128-encoder'
var code128= new Encoder()

export default {
  name: 'Users',
  data () {
    return {
      // maxLeds: 24,
      deviceip:null,
      apiUrl:null
